import React from 'react'
import { Link } from 'react-router-dom'
import { useState } from 'react';
import { useSelector,useDispatch } from 'react-redux'
import { useEffect } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function Shopcart() {
let basket=useSelector(state=>state.basketReducer);

let dispatch=useDispatch();
const [CurrentCount,SetCurrentCount]=useState()
const [total,Settotal]=useState(0)

useEffect(()=>{
 let a=basket.reduce((acc,item)=>acc+(item.price*item.quantity),0)
Settotal(a);

},[CurrentCount,basket])

const AddMore=(item)=>{
  let current=basket.find(element=>element.id===item.id)
  SetCurrentCount(current.quantity++);
}
const DecreaseMore=(item)=>{
  let current=basket.find(element=>element.id===item.id)
  if(current.quantity>0){
    SetCurrentCount(current.quantity--);
  }
}
const handleInpCount=(e,id)=>{
  let current=basket.find(element=>element.id===id)
  current.quantity=e.target.value;
}

const RemoveCart=(id)=>{
  dispatch({type:"RemoveItem",payload:id})
  toast(" Product removed from Your Cart!",{
    position: "top-right",
    autoClose: 500,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    });
}

const buy=()=>{
  alert("mliyyat icradadr")
}

  return (
    <div>

          <div className="CartPage-bg py-5">
          <h1 className='text-center display-3 fw-bold text-white py-2'>Your Cart</h1>
          <nav aria-label="breadcrumb" className='text-center text-white'>
            <ol className="breadcrumb justify-content-center ">
              <li className="breadcrumb-item"><Link className='text-white' to={"/"}>Home</Link></li>
              <li className="breadcrumb-item text-white" aria-current="page">Your Cart</li>
            </ol>
          </nav>
          </div>
           <div className="container py-5">
       {
         basket.length?  <div className="row">
         <div className="col-md-8 col-lg-9 table-responsive">
         <table className="table border table-hover">
                 <thead className='bg-light py-5'>
                   <tr>
                   <th><h4 className='mx-5'>Product</h4></th>
                   <th className='text-center'><h5>Price</h5></th>
                   <th className='text-center px-4'><h5 >Quantity</h5></th>
                   <th className='text-center px-3'><h5 >Total</h5></th>
                   </tr>
                 </thead>
                 <tbody>
 
                   {
                     basket&&
                     basket.map((item,index)=>(
                         <tr className='border' key={item.id}>
                           <td className='d-flex align-items-center'>
                             <img className='ShopCartImg' src={item.url} alt="" />
                             <p className='mx-3 w-75'>{item.title}</p>
                             <button className='btn btn-danger' onClick={(e)=>RemoveCart(item.id)}>Remove</button>
                           </td>
                           <td className='align-middle'>
                             <h3 className='px-5'>{item.price}$</h3>
                           </td>
                           <td className=' align-middle'>
                         <div className='input-group'>
                             <button className='btn btn-primary' onClick={(e)=>DecreaseMore(item)}>-</button>
                             <input type="number" className='form-control text-center ' disabled onChange={(e)=>handleInpCount(e,item.id)} min="0" value={item.quantity}  name="" id="" />
                             <button className='btn btn-primary' onClick={(e)=>AddMore(item)}>+</button>
                         </div>
                           </td>
                           <td className='align-middle '>
                           <h5 className='totalPrice'>{item.quantity*item.price}$</h5>
                           </td>
                       </tr>
                     ))
                   }
                 </tbody>
             </table>
         </div>
         <div className="col-lg-3">
           <div className='border py-4 px-3'>
             <h3 className='mb-3'>CART TOTALS</h3>
             <div className='d-flex border-bottom'>
               <h5>Subtotal:</h5>
               <h5 className='mx-5'>{total}$</h5>
             </div>
             <div className='d-flex'>
               <h5>Shipping:</h5>
               <h6 className='mx-2 mt-1'> There are no shipping methods available. Please double check your address, or contact us if you need any help.</h6>
             </div>
             <div className='text-center'>
             <button className='btn btn-primary px-5 mt-3 w-75 rounded-pill' onClick={()=>buy()}>Buy</button>
             </div>
           </div>
         </div>
         </div> 

         :<div className='text-center py-4'>
           <h1>Your cart is empty. Wanna go to shop? </h1>
           <Link to={"/allProducts"} className="goto">SHOP</Link>
         </div>
       }
          </div> 
          <ToastContainer />
     </div>
  )
}

export default Shopcart