import React from 'react'
import { useForm } from "react-hook-form";
function Login() {

  const { register, handleSubmit, formState: { errors } } = useForm();
  const onSubmit = data => console.log(data);
  return (
    <div className='container'>
    <div className="row py-96">
      <div className="col-md-6 px-md-5 mb-5">
        <h1 className='mb-4'>Login</h1>
        <form onSubmit={handleSubmit(onSubmit)}>
      <div className='position-relative'>
      <input className='form-control ' placeholder='Enter Email address or Phone' {...register("Login", { required: true, maxLength: 20 })} />
      {errors.Login?.type === 'required' && <span className='error'>Email address or Phone is required</span>}
      </div>
      <div className="position-relative">
      <input className='form-control my-4' placeholder='Password' {...register("Password",{required: true, pattern: /^[a-z0-9]+$/i,minLength:8 })} />
      {errors.Password?.type === 'required' && <span className='error'>Password is required</span>}
      {errors.Password?.type === 'pattern' && <span className='error'>Password is incorrect</span>}
      {errors.Password?.type === 'minLength' && <span className='error'>Password is short, at least 8 symbols</span>}
      </div>
      <button className='btn btn-primary px-4 mt-2'>Login</button>
    </form>
      </div>
      <div className="col-md-6 px-md-5">
        <h1 className='mb-4'>Sign Up</h1>
        <form onSubmit={handleSubmit(onSubmit)}>
      <div className='row justify-content-between'>
      <div className='position-relative col-md-6 mb-4'>
        <input className='form-control ' placeholder='Firstname' {...register("Firstname", { required: true, maxLength: 20 })} />
        {errors.Firstname?.type === 'required' && <span className='error'>Firstname is required</span>}
      </div>

     <div className='position-relative col-md-6 mb-4'>
      <input className='form-control ' placeholder='Lastname' {...register("Lastname", { required: true, maxLength:20 })} />
        {errors.Lastname?.type === 'required' && <span className='error'>Lastname is required</span>}
     </div>
      </div>

      <div className="position-relative">
      <input className='form-control my-2' placeholder='Enter email address or phone' {...register("email",{required: true,minLength:5 })} />
      {errors.email?.type === 'required' && <span className='error'>Email or phone is required</span>}
      </div>
      <div className="position-relative">
      <input className='form-control my-4' placeholder='Password' {...register("Password",{required: true, pattern: /^[a-z0-9]+$/i,minLength:8 })} />
      {errors.Password?.type === 'required' && <span className='error'>Password is required</span>}
      {errors.Password?.type === 'pattern' && <span className='error'>Password is incorrect</span>}
      {errors.Password?.type === 'minLength' && <span className='error'>Password is short, at least 8 symbols</span>}
      </div>
      <button className='btn btn-success px-5'>Sign Up</button>
    </form>
      </div>
    
    </div>
    </div>
  )
}

export default Login