import React from 'react'
import logo from '../images/logo.webp'
import {Link, NavLink} from 'react-router-dom'
import { useState } from 'react'
import { useSelector } from 'react-redux'
function Header() {
  let basket=useSelector(state=>state.basketReducer);
  let wishlist=useSelector(state=>state.wishlistReducer);
const [fixed,Setfixed]=useState(false)

window.addEventListener("scroll",()=>{
 if(window.scrollY>200){
  Setfixed(true)
 }
 else{
  Setfixed(false)
 }
})


  return (
    <div className={fixed? 'header py-2 fixed-top shadow':'header py-2' }>
        <div className="container">
        <nav className="navbar navbar-expand-lg navbar-light px-md-5">
    <Link className="navbar-brand" to={"/TimeZone"}><img src={logo} alt="" /></Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse justify-content-end " id="navbarSupportedContent">
     
      <ul className="navbar-nav mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink className="nav-link" to={"/TimeZone"}>Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to={"/allProducts"}>All Products</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to={"/login"}>Login</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to={"/panel"}>Admin Panel</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to={"/wishlist"}>
          <i className='fa-regular fa-heart'></i>
            <span className='Item-Counter text-danger fw-bolder'>{wishlist.length? `(${wishlist.length})`:""}</span></NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to={"/shopcart"}>
            <i className="fa-solid fa-cart-shopping"></i>
            <span className='Item-Counter text-danger fw-bolder'>{basket.length? `(${basket.length})`:""}</span> </NavLink>
        </li>
      </ul>
    </div>
  </nav>
        </div>
    </div>
  )
}

export default Header