let allData=[];

function allDataReducers(state=allData,action){
  switch(action.type){
    case "All_Data":
      return state=action.payload;
    case "ADD_Post":
      console.log(state=[...state,action.payload])
      return state=[...state,action.payload];
      
    default:
      return state;
  }
}
export default allDataReducers