import React from 'react'
import { useState,useEffect } from 'react';
import {Link} from 'react-router-dom'
import image from '../images/sliderImg/s1.png'
import heartImage from '../images/heart.gif'
import s3image from '../images/s3.webp'
import t1image from '../images/t1.png'
import { useSelector,useDispatch } from 'react-redux'
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import "swiper/css/navigation";
import { Navigation } from "swiper";
import Card from '../components/Card';

function Home() {
  let allData=useSelector(state=>state.allDataReducers);
   let basket=useSelector(state=>state.basketReducer);
  let wishlist=useSelector(state=>state.wishlistReducer);
  let dispatch=useDispatch();

  const addBasket=(item)=>{
        let currentItem=basket.find(index=>index.id==item.id)
        if(currentItem){
          currentItem.quantity+=1;
        }
        else{
          dispatch({type:"AddBasket",payload:item})
        }
  }

  const addWishlist=(item)=>{
    let currentItem=wishlist.find(index=>index.id==item.id);
    if(currentItem){
      dispatch({type:"RemoveWishlist",payload:item.id})
    }
    else{
      dispatch({type:"AddWishlist",payload:item})
    }
}
  return (
    <div>
    <div className="container-fluid bg-light">
    <div className="container slider ">
          <div className="row h-100 justify-content-around">
            <div className="col-md-8 slider-text">
              <h1 className='sliderTitle mb-5'>Select Your New Perfect Style</h1>
              <p className=''>We create unique, skeletonized automatic watches built around
               complex hand-assembled mechanical movements that run off of the kinetic energy you
                generate while wearing them. </p>
                <a href="#" className='btn-Shop mt-5'> SHOP NOW</a>
            </div>
          <div className="col-md-4 h-100 col-lg-3 bounce d-none d-sm-block">
           <img src={image} alt=""  />
           </div>
          </div>
        </div>
    </div>
      <div className="container py-96 px-3">
        <h1 className="fw-normal display-3">New Arrivals</h1>
          <div className="products mt-5">
             <div className="row">
              {
            
            allData &&
            allData.slice(0,3).map((item,index) =>(

           <div className="col-md-6 col-lg-4 " key={index}>
           <Card item={item} />
          </div>

             ) )
          }
             </div>
          </div>
          <div className="row py-96 align-items-center">
            <div className="col-md-6 pb-5">
              <h1 className='display-3 fw-bolder'>
              Watch of Choice
              </h1>
              <p className='my-5'>Vostok-Europe watches are able to handle all types of 
              extreme conditions as they are designed for the most extreme sports. They 
              have tested racing watches in the Dakar Rally; known as one of the most intense 
              car races in the world. They have also tested space watches at the edge of the earths 
              atmosphere and beyond into the depths of
               space. There is really no better durable men's watch than a Vostok-Europe.
                </p>
                <Link to="/allProducts" className='btn btn-danger px-5 py-3 fw-bold'>SHOW WATCHES</Link>
            </div>
            <div className="col-md-6">
                <img className='w-100 h-100 rounded' src={s3image} alt="" />
            </div>
          </div>

          <div className="row py-96">
            <h1 className='text-center'>Some Features that Made us Unique</h1>
            <p className='text-center fs-6 text-muted mb-md-5 pb-5'>Who are in extremely love with eco friendly system.</p>
            <div className="col-lg-3 mt-5">
              <div className='text-center bg-light p-3 rounded mb-5 service-box'>
              <i className="text-primary fs-2 fa-solid fa-rocket"></i>

              <h3 className='my-3'>High Performance</h3>
              <p style={{textAlign:"justify"}}>
              Welcome to the world of high accuracy quartz or high end quartz, generally categorized as watches
               that are specified to plus or minus 10 seconds a year. 
              </p>
             </div>
              <div className='text-center bg-light p-3 rounded mb-5 service-box'>
              <i className="text-primary fs-2 fa-solid fa-gear"></i>

              <h3 className='my-3'>High Performance</h3>
              <p style={{textAlign:"justify"}}>
              To open the Settings app on Apple Watch: When viewing the time on your Apple Watch, press the Digital Crown 
              (round button) once to open the Home screen. 
              </p>
             </div>
            </div>
            <div className="col-lg-6 text-center">
              <img src={t1image} className="w-75" alt="" />
            </div>
            <div className="col-lg-3 mt-5">
              <div className='text-center bg-light p-3 rounded mb-5 service-box'>
              <i className="text-primary fs-2 fa-solid fa-building"></i>

              <h3 className='my-3'>Free Shipping </h3>
              <p style={{textAlign:"justify"}}>
              But the truth is that, like virtually everything else, free shipping is not actually free. 
              With conditional shipping, you may be more likely to have a higher average order value  
              </p>
             </div>
              <div className='text-center bg-light p-3 rounded service-box'>
              <i className="text-primary fs-2 fa-solid fa-phone"></i>

              <h3 className='my-3'>Great Support</h3>
              <p style={{textAlign:"justify"}}>
              Usage of the Internet is becoming more common due to rapid advancement of technology and power.
              </p>
             </div>
            </div>
          </div>
          <div className="row py-96">
            <h1 className='display-4 text-center fw-bold mb-4'>Popular Watches</h1>
          <Swiper 
      spaceBetween={50}
      slidesPerView={3}
      navigation={true}
      modules={[Navigation]}
      breakpoints={{
        // when window width is >= 640px
        320: {
          width: 320,
          slidesPerView: 1,
        },
        768: {
          width: 768,
          slidesPerView: 2,
        },
        
        1400: {
          width: 1400,
          slidesPerView: 3,
        }
      }}
      
    >
        {allData&&
        allData.map((item,index)=>(
       <SwiperSlide key={index} >
         <Card item={item}/>  
       </SwiperSlide>
        ))

        }          
      
    </Swiper>
          </div>
      </div>
    </div>
  )
}

export default Home