import axios from 'axios';
import React from 'react'
import { useForm } from "react-hook-form";
import { useSelector,useDispatch } from 'react-redux';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function Panel() {
  let allData=useSelector(state=>state.allDataReducers);
  const { register, handleSubmit,formState: { errors },reset } = useForm();
  let dispatch=useDispatch()
  
  const onSubmit = (inputs,e) => {

    inputs.quantity=1;
    axios.post("https://my-json-server.typicode.com/T-Ulvi/DB/products",inputs).then(res=>res.data)
    .then(res=>dispatch({type:"ADD_Post",payload:res})) 
    toast(" Product successfully added!",{
      position: "top-right",
      autoClose: 500,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      }); 
    e.target.reset();

  }
  return (
    <div>
       <div className="container py-96">
         <div className="row justify-content-center w-100 px-3">
           <div className=" col-md-6">
           <h1 className='mb-4 text-center'>Add Product</h1>
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className='position-relative py-3'>
            <input className='form-control'   placeholder="Product's name"  {...register("title",{required:true})} />
            {errors.name && <p className='error'>Product's name is reqiured</p>}
            </div>
           <div  className='position-relative py-2'>
           <input className='form-control my-1'  placeholder="Product's price" {...register("price",{required:true})} />
            {errors.price && <p className='error'>Product's price is reqiured</p>}
           </div>
          <div className='position-relative py-1'>
          <input className='form-control my-2'  placeholder="Product's image (url)" {...register("url")} />
            {errors.price && <p className='error'>Product's price is reqiured</p>}
          </div>
            <button className='btn btn-success mt-3' type="submit">Submit</button>
        </form>
           </div>
         </div>
       </div>
       <ToastContainer/>
    </div>
  )
}

export default Panel