import React from 'react'
import { useSelector,useDispatch   } from 'react-redux';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import Card from '../components/Card';
function AllProducts() {
  let allData=useSelector(state=>state.allDataReducers);
  const [count, Setcount]=useState(4);
const LoadMore=(e)=>{
  e.preventDefault()
  Setcount(count+4);
}

  return (
    <div className='container-fluid p-0'>

      <div className='allPro-bg bg-info py-5'>
      <h1 className='text-center display-3 fw-bold text-white py-2'>All products</h1>
      <nav aria-label="breadcrumb" className='text-center text-white'>
        <ol className="breadcrumb justify-content-center ">
          <li className="breadcrumb-item"><Link className='text-white' to={"/"}>Home</Link></li>
          <li className="breadcrumb-item text-white" aria-current="page">Library</li>
        </ol>
      </nav>
      </div>

      <div className="container">
      <div className="row py-96" >
      {       allData&&
              allData.slice(0,count).map((item,index) =>(
              <div className="col-md-6 col-lg-3" key={index}>
                    <Card item={item}/>
              </div>
             ) )
         
          }
           <div className='text-center py-2'>
           <a href='#' className='btn btn-primary px-5 mt-3' onClick={(e)=>LoadMore(e)}>Load More</a>
           </div>
      </div>
      </div>
    </div>
  )
}

export default AllProducts