import React from 'react'
import heartImage from '../images/heart.gif'  
import { useSelector,useDispatch } from 'react-redux'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function Card(props) {
  let basket=useSelector(state=>state.basketReducer);
  let wishlist=useSelector(state=>state.wishlistReducer);
  let dispatch=useDispatch();

  const addBasket=(item)=>{
    toast(" Product added to cart!",{
      position: "top-right",
      autoClose: 500,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      }); 
        let currentItem=basket.find(index=>index.id===item.id)
        if(currentItem){
          currentItem.quantity+=1;
        }
        else{
          dispatch({type:"AddBasket",payload:item})
        }
  }

  const addWishlist=(item)=>{
     
    let currentItem=wishlist.find(index=>index.id===item.id);
    if(currentItem){
      dispatch({type:"RemoveWishlist",payload:item.id})
      toast(" Product removed from wishlist!",{
        position: "top-right",
        autoClose: 500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        });
    }
    else{
      dispatch({type:"AddWishlist",payload:item})
      toast(" Product added to wishlist!",{
        position: "top-right",
        autoClose: 500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        });
    }
}
  return (

    <>
    <div className="card">
      
      <div className="card-img">
      <img src={props.item.url} className="card-img-top" alt="..."/>
    
      <button className='heart' onClick={(e)=>addWishlist(props.item)}>

     {wishlist.find(element=>element.id===props.item.id)?
         <img src={heartImage} alt="..."/>:<i className={"fa-regular fa-heart"}></i>
     }
     
 
      </button>
      <button onClick={(e)=>addBasket(props.item)} className='addCart btn btn-success px-4'>Add to Card</button>
      </div>
      <div className="card-body d-flex">
      <h4 title={props.item.title} className="card-title me-auto w-75">{props.item.title.length>13? props.item.title.slice(0,13)+"...":props.item.title}</h4>
        <h3 className='text-danger'>{props.item.price}$</h3>
      </div>
    </div>
    <ToastContainer />
    </>
  )
}

export default Card