let wishlist=[];

function wishlistReducer(state=wishlist,action){
  switch(action.type){
    case "AddWishlist":
      return state=[...state,action.payload];
    case "RemoveWishlist":
      return state=state.filter(item=>item.id!==action.payload)
    default:
      return state
  }
}
export default wishlistReducer;